import React,{Component} from 'react'

export default class Release extends Component{
    render(){
        return (
            <div className="release">
                <div className="repliesItem">
                    <p><span className="iconfont icon-biaoti"></span></p>
                    <input type="text" placeholder=""/>
                </div>
                <div className="repliesItem">
                    <p><span className="iconfont icon-fenlei"></span></p>
                    <select>
                        <option value=""></option>
                        <option value="share"></option>
                        <option value="ask"></option>
                        <option value="job"></option>
                    </select>
                </div>
                <div className="repliesItem">
                    <p><span className="iconfont icon-icon2"></span></p>
                    <textarea name="" id="" cols="" rows=""></textarea>
                    <div>
                        <button className="repliesBtn"></button>
                    </div>
                </div>
            </div>
        )
    }
}


